h1 {
  letter-spacing: 0.8em;
  background: #f0b7a1;
  background: linear-gradient(
    to right,
    #f0b7a1 0%,
    #8c3310 50%,
    #752201 51%,
    #bf6e4e 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

form {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.form-grp {
  margin-bottom: 10px;
}
label {
  font-weight: bold;
  margin-bottom: 5px;
  background: #f0b7a1;
  background: linear-gradient(
    to right,
    #f0b7a1 0%,
    #8c3310 50%,
    #752201 51%,
    #bf6e4e 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
input {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 30px;
}
.button {
  margin-top: 10px;
  background-color: #007bff;
  color: white;
  padding: 8px 15px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}
